@mixin loadingGradient() {
  color: transparent !important;
  background-color: $gray-200 !important;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: linear-gradient(to right, rgba($gray-100,0) 30%, rgba($gray-400,.75) 50%, rgba($gray-100,0) 80%);
  background-size: 200% 100%;
  backface-visibility: hidden;
}

@mixin smallDividerBottom {
  position: relative;

  &:after {
    display: block;
    @include primaryGradient;
    //background-color: ;
    content: "";
    height: 3px;
    bottom: -$spacer5;
    width: 10%;
    position: absolute;
    left: 0%;
    //margin-left: -5%;

  }
}


@mixin primaryGradient($deg: 115deg) {
  @include gradient-directional($purple,$primary,$deg)
}

@mixin primaryGradientBorder() {
  border-image: linear-gradient(115deg,$purple,$primary);
}


@mixin filter($filter) {
  -webkit-filter: $filter; /* Safari 6.0 - 9.0 */
  filter: $filter;
}

@mixin text-gradient {
  color: $primary;
  @include primaryGradient;
  text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  &:before {
    @include primaryGradient;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
}