.ccomment {
  padding: $spacer3;
  border-radius: $border-radius;
  margin-top: $spacer5;
  background-color: $gray-100;


  h4 {
    font-size: $h5-font-size;
  }

  button[type="submit"] {
    background-color: $primary !important;
    @extend .btn-primaryGradient;

    @include hover-focus-active {
      background-position: center center;

    }
  }
}
