
html, body {
  min-height: 100%;
  height: 100%;
  position: relative;
}

body {


  &:not(.view-featured) {
    display: flex;
    flex-direction: column;

    main {
      flex: 1 0 auto;
      width: 100%;
    }
  }


  height: 100%;
}



body.modal {
  width: 100%;
  padding: 0 $grid-gutter-width;

  .page-header {
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
  }
}


header,
footer {
  flex: none; /* 2 */
}


a.expand-header {
  background-color: $primary;
  color: #FFF !important;
  position: absolute;
  z-index: 2;
  left: 50%;
  margin-left: -40px;
  width: 80px;
  text-align: center;
  transition: all 250ms ease;
  transition-delay: 600;

  &.active {
    i {
      transition: scaleY(-1);
    }
  }
}

a:focus, button, button:focus {
  outline: 0 !important
}


html.desktop body,html.desktop .fancybox-slide,.textCol .wrapper {
  &::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.1);
    background-color: $light;
    padding: .2em;
  }

  &::-webkit-scrollbar
  {
    width: .65em;
    background-color: $light;
  }

  &::-webkit-scrollbar-thumb {
    //background-color: $primary;
    @include gradient-x($purple, $primary);
  }
}
