.navGraphs {
  @include primaryGradient();
  display: flex;
  //border-top:1px solid darken($red,10%);
  box-shadow: -5px 0 5px rgba($black,.2);
  position: relative;
  z-index: 3;

  .nav-item {
    color: $white;
    padding: $spacer2;
    font-size: $font-size-sm;
    text-align: center;

    @include hover-focus-active {
      text-decoration: none;
    }

    &.active {
      font-weight: bold;
    }
  }
}




.social-sharing  {
  a {
    cursor: pointer;
  }
}


body.view-article .pagenav {
  margin: 0;
  margin-top: $spacer5;
  padding: 0;
  display: flex;
  justify-content: space-between;
  padding: $spacer2 0;
  border-top: 1px solid $gray-200;

  li {

    list-style: none;
    height: 100%;
    display: flex;

    a {
      color: $body-color;
      display: flex;
      align-items: center;
      padding: $spacer2;
      font-size: $font-size-sm;
      height: 100%;

      @include hover-focus-active {
        text-decoration: none;
      }
    }

    i.fa-2x {
      font-size: 3.5rem;
      transition: $transition-base;
    }


    &.previous {
      @include hover-focus-active {
        i {
          transform: translate(-0.5rem, 0);
        }
      }

    }

    &.next {
      align-self: flex-end;
      margin-left: auto;

      @include hover-focus-active {
        i {
          transform: translate(.5rem, 0);
        }
      }

    }

  }
}