.tags {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}

.tag {
  @extend .badge;
  margin-right: $spacer1;
  margin-bottom: $spacer1;
  font-weight: $font-weight-base;
  color: $red;

  &.tag-primarySpecial {
    @include primaryGradient;
    border-color: darken($purple, 7%);
    transition: $transition-base;
    background-size: 100%;
    color: $white;


    @include hover-focus-active {
      background-size: 150%;
      color: rgba($white,.85);
      border-color: darken($purple, 10%);
    }
  }

  .tag-count {
    background-color: rgba($gray-900,.4);
  }

}

a.tag {

}


.tagsSimilar {
  padding-top: $spacer5;

  .post {
    margin-bottom: $spacer5;
  }
}
