

/************** FOOTER ***************/


footer {
  //border-top: 3px solid $primary;
  background-color: $gray-100;
  width: 100%;
  position: relative;
  margin-top: $spacer5;

  @include media-breakpoint-down(sm) {
    padding-bottom: $mobileBarNavHeight;
  }

  a.logo {
    $logoWidth: 12em;
    $logoHeight: $logoWidth/4.6;
    display: block;
    width: $logoWidth;
    height: $logoHeight;
    position: absolute;
    left: 50%;
    top:-($logoHeight*0.5);
    margin-left: -$logoWidth/2;
    background-image: url(../img/logo_black.svg);
    background-size: contain;
    background-position: center top;
    background-repeat: no-repeat;
    color:transparent;
    overflow: hidden;
  }



  .articleModules {
    padding: $spacer5*2 0;

    @include media-breakpoint-down(sm) {
      padding: $spacer5 0;
    }


    @include media-breakpoint-up(sm) {
      .personalStravaStats {
        margin: 0;

        .introText {
          display: none;
        }

        .followBtnWrapper {
          display: none;
        }

        .strava-link {
          .small {
            display: none;
          }

          img {
            max-height: 20px;
          }
        }
      }
    }

  }

  .borderBottomHeadline {
    margin-bottom: $spacer4;
  }

  .module {
    margin-bottom: 0;
  }

  .menuModules {
    background-color: rgba($gray-200,.75);
    font-size: $font-size-small;
    padding: $spacer2 0;

    .menu {
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      flex-wrap: wrap;




      li {
        margin-right:  $spacer3;
        list-style: none;

        @include media-breakpoint-down(sm) {
          width: 100%;
          margin: 0;
        }

        a {
          color: $body-color;
          font-size: $font-size-small;
          white-space: nowrap;
          display: block;
          text-align: center;
          padding: $spacer2;
          @include media-breakpoint-down(sm) {
            padding: $spacer1 $spacer2;

          }

        }

        .post {
          margin-bottom: $spacer3;
        }

        .postLinkItem {
          padding: 0;
          margin: 0;
        }
      }
    }


  }

}