
.mobileBottomBar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  background-color: $white;
  display: flex;
  flex-direction: column;
  z-index: $zindex-modal;
  box-shadow: 0 -2rem 1rem rgba($black, .25);


  .nav {
    @include primaryGradient;
    position: absolute;
    width: 100%;
    left: 0;
    height: $mobileBarNavHeight;
    top: $mobileBarNavHeight * -1;
    padding: 0 $spacer2;


    .mobileBottomBarToggle {
      opacity: 0;
      transition: $transition-base;
      pointer-events: none;
    }

    .btn-Likes {
      box-shadow: none;

      span {
        display: none;
      }

      i {
        margin: 0;
        color: $white;
      }
    }

    .nav-item,.btn-Likes {
      color: $white;
      border-right: 1px solid rgba($dark, 0.15);
      padding: 0 $spacer2;
      display: flex;
      align-items: center;
      position: relative;
      height: 100%;


      @media (max-width: $grid-breakpoint-xxs) {
        padding: 0 $spacer1;
      }

      @include hover-focus-active {
        text-decoration: none;
        color: $white;
      }


      &.mobileBottomBarToggle {
        margin-left: auto;
        border-right: 0;

        //border-left: ;
      }

      &.btnccomment {
        //border-right-width: 2px;
      }

      //&.btnContentAnchor {
      //  .fa-caret-up {
      //    position: absolute;
      //    font-size: 12px;
      //    right: 6px;
      //    top: 2px;
      //    color: rgba($white,.8);
      //  }
      //
      //}
    }


  }

  .tab-content {
    flex-grow: 1;
    overflow: auto;
    transition: $transition-base;
    height: 0vh;
  }

  .tab-pane {
    padding: $spacer3;
    height: 100%;
  }

  &.expand {

    .nav-item {

      @include hover-focus-active {
        @include gradient-directional(darken($purple, 7%), darken($primary, 7%), 115deg);
      }

      &.active {
        @include gradient-directional(darken($purple, 7%), darken($primary, 7%), 115deg);
      }
    }

    .tab-content {
      height: 60vh;
    }

    .mobileBottomBarToggle {
      opacity: 1;
      pointer-events: all;
    }
  }
}