// Scroll Nav Menü
$navbarShadow: 0 0px 20px rgba(0, 0, 0, 0.25);

.nav-transition {
  transition: color 250ms ease;
}



// Standard Joomla Menü
$navbarShadow: 0 0px 20px rgba(0, 0, 0, 0.25);

@include media-breakpoint-down(sm) {
  .navbar-collapse {
    border-color: #000;
  }
}

.navbar {
  position: absolute;
  top:0;
  width: 100%;
  left:0;
  padding: $spacer3 0;
  z-index: 3;

  ul {
    display: none;
  }

  .navbar-brand {
    padding: 0;
    margin: 0 $spacer3;
  }
}

.navbar-default .navbar-collapse, .navbar-default .navbar-form {
  //border-color: $dark;
}
