
// Offline
.offline,.error {
  #hero-area .wrapper {
    padding: 5vh 0;
  }
  .btn-nav-toggle {
    display: none !important;
  }

  h1,h2 {
    text-shadow: none !important;
  }

  .btn.margin-top {
    margin-top: $grid-gutter-width*2;
  }

  #form-login {
    max-width: 400px;
    margin: 0 auto;
  }


  .card {
    background-color: rgba(#000,0.25);
  }

  hr {
    background: rgba(#000, 0.1);
    border-width: 0px;
    position: relative;
    z-index: 2;
    height: 3px;
    display: block;
    width: 100%;
  }
}



