#hero-area {
  width: 100%;
  text-align: center;
  position: relative;

  img.bg-image {
    position: absolute;
    object-fit: cover;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .5;
  }

  @include media-breakpoint-down(sm) {
    @media (orientation: landscape) {
      height: auto;
      padding-bottom: 150px;
    }
  }

  .tooltip {
    font-size: $font-size-base;
  }

  .wrapper {
    //padding-top: $grid-gutter-width * 3;
    position: relative;
    z-index: 2;
    min-height: 100vh;
    display: flex;
    background-color: $black;

    @include media-breakpoint-down(sm) {
      padding: $grid-gutter-width*2 0;
      padding-top: 28vw;
    }
  }

  .container {
    display: flex;
  }

  .logo {
    margin-right: auto;
    max-width: 15em;
  }

  .intro {
    color: #FFF;
    padding: 0 $grid-gutter-width/2;
    flex-grow: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: $spacer3;

    @include media-breakpoint-down(sm) {
      padding: 0;
    }
  }

  .text {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    flex-direction: column;
  }

  .btnStrava {
    color: $white;
  }

  .btnToBlogWrapper {
    padding-top: 5vh;

    @media (max-width: $grid-breakpoint-xxs) {
      padding-top: 2vh;

    }
  }

  .btnToBlog {
    box-shadow: $box-shadow-sm, $box-shadow;
    transition: $transition-base;
    will-change: transform;

    @include hover-focus-active {
      transform: scale(1.05);
    }

    @include media-breakpoint-down(sm) {
      //font-size: $font-size-base;
    }

  }

  .blogContent {
    height: 100%;
    padding: $spacer3 $spacer2;
    padding-bottom: $spacer5;
    position: relative;
    z-index: 4;
    display: flex;
    justify-content: center;
    flex-direction: column;

    .latestPosts {

    }

    .rblHeroAreaHeadline {
      padding: $spacer2 5%;
      padding-left: 10%;
      transform: translate(1em, 0);
      white-space: nowrap;
      min-width: 80%;
    }


    h1, .fp-introtext {
      text-shadow: 0 0 20px rgba(0, 0, 0, 1);
    }

    h1 {
      @include media-breakpoint-down(sm) {
        font-size: $h4-font-size;
        padding-top: 2vh;
      }
    }
  }
}


.hero-area-blog {
  width: 100%;
  padding: 0 !important;
  text-align: center;
  position: relative;
  background-color: $black;

  &:before {
    content: "";
    padding-top: 62.5%;
    display: block;
  }

  @include media-breakpoint-up(sm) {
    min-height: 40vh;

    &:before {
      display: none;
    }
  }

  &.default-image {
    @include media-breakpoint-down(sm) {
      @media (orientation: portrait) {
        min-height: 25vh;

        &:before {
          display: none;
        }
      }
    }

  }

  &:after {
    content: "";
    display: block;
    height: 6em;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    @include gradient-y(rgba($layout-dark, .75), rgba($layout-dark, 0));
  }


  img.bg-image {
    position: absolute;
    object-fit: cover;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include loadingGradient;
  }

  .rblHeroAreaHeadline {
    position: absolute;
    z-index: 2;
    margin: 0;
    min-width: 5em;

    right: 0;
    top: $spacer3;
    transform: none;
  }


}
