//Images

.hover-zoom-img {
  position: relative;
  overflow: hidden;

  .img-wrapper-cover {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;

    a {
      display: flex;
      width: 100%;
    }

    picture {
      width: 100%;
    }

    img {
      transition: transform 500ms ease-in-out;
      transform: translate3d(0, 0, 0);
      object-fit: cover;
      width: 100%;
      height: 100%;
      will-change: transform;
    }

    &.rounded-circle {
      img {
        border-radius: 50%;
      }
    }
  }

  &.img-thumbnail {
    background-color: $white;

    .img-wrapper-cover {
      overflow: hidden;
      background-color: $white;
      border: $thumbnail-padding solid $white;
    }
  }

  &.post-image-16-9 {
    &:before {
      content: "";
      padding-top: 56.25%;
      display: block;
    }
  }

  &.post-image-21-9 {
    &:before {
      content: "";
      padding-top: 42.86%;
      display: block;
    }
  }

  &.post-image-16-10 {
    &:before {
      content: "";
      padding-top: 62.5%;
      display: block;
    }
  }

  &.post-image-4-3 {
    &:before {
      content: "";
      padding-top: 33.33%;
      display: block;
    }
  }

  &.post-image-1-1 {
    &:before {
      content: "";
      padding-top: 100%;
      display: block;
    }
  }

  &.border-sm {
    .img-wrapper-cover {
      border-width: $imgBorderWidth-sm;
    }
  }

  &:not(.no-hover-zoom) {
    &:hover {
      html.desktop & {
        .img-wrapper-cover {
          img {
            transform: scale(1.07);
          }
        }

        .img-wrapper-cover.rounded-circle {
          img {
            transform: scale(1.2);
          }
        }
      }
    }
  }

}

