.moreImagesIconWrapper {
  text-align: center;
  color: rgba($black, .5);
  cursor: pointer;
  margin-top: -$spacer4;

  .moreImagesIcon {
    border-radius: $border-radius-sm;
    background-color: $gray-200;
    display: inline-flex;
    padding: $spacer1;
    margin-bottom: $spacer2;
    font-size: 6px;
    transition: $transition-base;

    i {
      margin: 0 2px;
    }


  }

  p {
    color: rgba($black, .5);
  }

  @include hover-focus-active {
    .moreImagesIcon {
      background-color: $gray-600;
      color: $white
    }

  }
}


