.item-image-content {

  margin-top: $spacer3;
  margin-bottom: $spacer3;

  &.sm-left {
    margin-top: 0;
    float: left;
    max-width: 50%;
    margin-right: $spacer4;
  }

  &.sm-right {
    margin-top: 0;
    float: right;
    max-width: 50%;
    margin-left: $spacer4;
  }

  a {
    color: inherit;
    text-decoration: inherit;

    @include hover-focus-active {
      text-decoration: none;
    }

    display: block;
    width: 100%;
  }


  .subtitle {
    display: block;
    font-size: small;
    color: inherit;
    padding: $spacer1;
    font-style: italic;
  }
}


