
// Icons

.fa-3x {
  font-size: 48px;
}


.fa-ul {
  padding-left: 0;
  margin-left: $fa-li-width;
  list-style-type: none;

  > li,
  > div {
    position: relative;
  }
}

.fa-li {
  position: absolute;
  left: -$fa-li-width;
  width: $fa-li-width;
  top: (2em / 10);
  text-align: center;

  .fa-lg {
    left: (-$fa-li-width + (4em / 14));
  }
}

i.fa-margin-right {
  margin-right: map_get($spacers, 2);
}

// Type#

.text-body-lighten {
  color: lighten($body-color, 15%) !important;
}



.text-gradient {
  @include text-gradient;
}

.fp-introtext {
  font-size: $h6-font-size;
  max-width: 650px;
  display: inline-block;
  text-align: center;
  font-weight: normal;
  //font-family: Arial, serif;
  text-rendering: optimizelegibility;
  @include media-breakpoint-up(md) {
    font-size: $font-size-base * 1.15;
  }
}

.rblHeroAreaHeadline {
  padding: $spacer2 $spacer3;
  padding-left: 3em;
  white-space: nowrap;
  color: rgba($white,.9);
  display: block;
  margin-bottom: $spacer5;
  background-image: url('../img/bg_hero_area_heading.svg');
  background-size: cover;
  background-position: left top;
  text-transform: uppercase;
  text-align: center;
}

.borderBottomHeadline {
  text-transform: uppercase;
  font-size: $h5-font-size;
  position: relative;
  margin-bottom: $spacer5;
  padding: $spacer2 0;
  border-bottom:$imgBorderWidth-sm solid $gray-200;
}


.rblHeadline {
  text-transform: uppercase;
  font-size: $h5-font-size;
  background-color: darken($gray-100,2%);
  position: relative;
  margin-bottom: $spacer4;

  span {
    display: inline-block;
    max-width: 70%;
    background-color: $white;
    position: relative;
    padding-right: .5em;

    &:after {
      display: block;
      background-color: $white;
      content: "";
      clip-path: polygon(0 0, 100% 0%, 15% 100%, 0% 100%);
      height: 100%;
      width: 5%;
      position: absolute;
      top: 0;
      right: -4.25%;

    }
  }

  .mobileBottomBar & {
    span {
      max-width: 100%;
    }
  }
}


.large, large {
  font-size: 115%;
}

.bold, b, strong {
  font-weight: $font-weight-bold !important;
}


.text-underline {
  text-decoration: underline;
}


a.no-hover:hover {
  text-decoration: none;
}

.clear {
  clear: both
}


.no-br {
  white-space: nowrap
}

p {
  text-rendering: optimizelegibility;
}

ul {
  padding-left: 20px
}




.clickable-element {
  cursor: pointer;
}

.fa {
  display: inline-block;
}



.fade.in {
  opacity: 1
}


.strava-link a, a.strava-link {

  color: $secondary;
  text-decoration: underline;
  text-align: center;
  display: block;

  img {
    max-width: 100px;
    margin: $spacer2 auto;
  }
}

a.anchor {
  display: block;
  position: relative;
  top: -30px;
  visibility: hidden;
}

